import React, {useEffect, useState} from 'react';
import {getUpbitList} from "../api/Api";

function API_1(props) {
    const [serverData, setServerData] = useState('')

    useEffect(() => {

        getUpbitList().then(data => {
            setServerData(data)
            console.log(data)
        })
    }, []);

    let krwList = [];
    let btcList = [];
    let usdtList = [];

    for (let key in serverData) {
        const marketKey = serverData[key].market;
        if (marketKey.startsWith("KRW-")) {
            krwList.push(serverData[key])
        } else if (marketKey.startsWith("BTC-")) {
            btcList.push(serverData[key])
        } else {
            usdtList.push(serverData[key])
        }
    }

    return (
        <div style={{ display: 'flex', justifyContent: 'space-between', padding: '10px', maxWidth: '1200px', margin: '0 auto' }}>
            {/* KRW List (좌측) */}
            <div style={{ border: '1px solid black', padding: '10px', maxWidth: '30%', flex: 1 }}>
                <h3>KRW Market : {krwList.length} 항목</h3>
                {krwList.map((item, index) => (
                    <div key={index} style={{ display: 'flex', borderBottom: '1px solid black', padding: '10px' }}>
                        <div style={{ flex: 1, borderRight: '1px solid black', padding: '5px', wordWrap: 'break-word' }}>
                            {item.korean_name} ({item.english_name})
                        </div>
                        <div style={{ flex: 1, padding: '5px', wordWrap: 'break-word' }}>
                            {item.market}
                        </div>
                    </div>
                ))}
            </div>

            {/* BTC List (중앙) */}
            <div style={{ border: '1px solid black', padding: '10px', maxWidth: '30%', flex: 1 }}>
                <h3>BTC Market : {btcList.length} 항목</h3>
                {btcList.map((item, index) => (
                    <div key={index} style={{ display: 'flex', borderBottom: '1px solid black', padding: '10px' }}>
                        <div style={{ flex: 1, borderRight: '1px solid black', padding: '5px', wordWrap: 'break-word' }}>
                            {item.korean_name} ({item.english_name})
                        </div>
                        <div style={{ flex: 1, padding: '5px', wordWrap: 'break-word' }}>
                            {item.market}
                        </div>
                    </div>
                ))}
            </div>

            {/* USDT List (우측) */}
            <div style={{ border: '1px solid black', padding: '10px', maxWidth: '30%', flex: 1 }}>
                <h3>USDT Market : {usdtList.length} 항목</h3>
                {usdtList.map((item, index) => (
                    <div key={index} style={{ display: 'flex', borderBottom: '1px solid black', padding: '10px' }}>
                        <div style={{ flex: 1, borderRight: '1px solid black', padding: '5px', wordWrap: 'break-word' }}>
                            {item.korean_name} ({item.english_name})
                        </div>
                        <div style={{ flex: 1, padding: '5px', wordWrap: 'break-word' }}>
                            {item.market}
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );

}

export default API_1;